import React, { Component } from "react";

import axios from "axios";
import { Link } from "react-router-dom";

class EditTodo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      priority: ""
    };
  }

  componentWillMount() {
    this.getMeetups();
  }
  handleInputChange(e) {
    const target = e.target;
    const value = target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  editToDo(newTodo) {
    axios
      .request({
        method: "put",
        url: `http://localhost:3000/api/todoApps/${this.state.id}`,
        data: newTodo
      })
      .then(response => {
        this.props.history.push("/");
      })
      .catch(err => console.log(err));
  }
  getMeetups() {
    let todoId = this.props.match.params.id;
    axios
      .get(`http://localhost:3000/api/todoApps/${todoId}`)
      .then(response => {
        this.setState(
          {
            id: response.data.id,
            name: response.data.todoname,
            priority: response.data.priority
          },
          () => {
            console.log(this.state);
          }
        );
      })
      .catch(err => console.log(err));
  }
  onSubmit(e) {
    e.preventDefault();
    const newTodo = {
      todoname: this.refs.todoname.value,
      //   todo: this.refs.todo.value,
      priority: this.refs.priority.value
    };
    this.editToDo(newTodo);
  }
  render() {
    return (
      <div>
        <br />
        <Link className="btn grey" to="/">
          Back
        </Link>
        <h1> Edit Todo Item </h1>
        <form onSubmit={this.onSubmit.bind(this)}>
          <div className="input-field">
            <input
              type="text"
              name="todoname"
              ref="todoname"
              onChange={this.handleInputChange.bind(this)}
              value={this.state.name}
            />
            <label htmlFor="name"> todo Name </label>
          </div>
          <div className="input-field">
            <input
              type="text"
              name="todo"
              ref="todo"
              onChange={this.handleInputChange.bind(this)}
            />
            <label htmlFor="todo"> todo </label>
          </div>
          <div className="input-field">
            <input
              type="text"
              name="priority"
              ref="priority"
              onChange={this.handleInputChange.bind(this)}
              value={this.state.priority}
            />
            <label htmlFor="priority"> priority </label>
          </div>
          <input type="submit" value="save" className="btn" />
        </form>
      </div>
    );
  }
}

export default EditTodo;
