import React, { Component } from "react";
import axios from "axios";
import TodoItem from "./TodoItem";

class Meetups extends Component {
  constructor() {
    super();
    this.state = {
      meetups: []
    };
  }

  componentWillMount() {
    this.getMeetups();
  }
  getMeetups() {
    axios
      .get("http://localhost:3000/api/todoApps")
      .then(response => {
        this.setState({ meetups: response.data }, () => {});
      })
      .catch(err => console.log(err));
  }
  render() {
    const todoItems = this.state.meetups.map((meetup, i) => {
      return <TodoItem key={meetup.id} item={meetup} />;
    });
    return (
      <div>
        <h1> Todo List </h1>
        <ul className="collection">{todoItems}</ul>
      </div>
    );
  }
}

export default Meetups;
