import React, { Component } from "react";

import axios from "axios";
import { Link } from "react-router-dom";

class AddTodo extends Component {
  addTodo(newTodo) {
    console.log(newTodo);
    axios
      .request({
        method: "post",
        url: "http://localhost:3000/api/todoApps",
        data: newTodo
      })
      .then(response => {
        this.props.history.push("/");
      })
      .catch(err => console.log(err));
  }

  onSubmit(e) {
    e.preventDefault();
    const newTodo = {
      todoname: this.refs.todoname.value,
      //   todo: this.refs.todo.value,
      priority: this.refs.priority.value
    };
    this.addTodo(newTodo);
  }
  render() {
    return (
      <div>
        <br />
        <Link className="btn grey" to="/">
          Back
        </Link>
        <h1> Add Todo Item</h1>
        <form onSubmit={this.onSubmit.bind(this)}>
          <div className="input-field">
            <input type="text" name="todoname" ref="todoname" />
            <label htmlFor="name">todo Name</label>
          </div>
          <div className="input-field">
            <input type="text" name="todo" ref="todo" />
            <label htmlFor="todo">todo</label>
          </div>
          <div className="input-field">
            <input type="text" name="priority" ref="priority" />
            <label htmlFor="priority">priority</label>
          </div>
          <input type="submit" value="save" className="btn" />
        </form>
      </div>
    );
  }
}

export default AddTodo;
